'use client'

import {useState} from 'react'
import * as R from 'remeda'
import {Delimiter} from '@nyx/kr-common/constant/Constant'
import {IP} from '@nyx/kr-common/util/Validation'
import IPCIDR from 'ip-cidr'

/**
 * https://github.com/ortexx/ip-cidr
 * https://mxtoolbox.com/subnetcalculator.aspx
 */
export default function KrIp() {
    const [cidr, setCidr] = useState<string>('192.168.0.1/24')
    const [ip, setIp] = useState<string>('192.168.0.1')
    const [calculatedCidr, setCalculatedCidr] = useState<any>() // XXX - IPCIDR 로 타입정의

    const handleCalculateButtonClick = () => {
        const address = IPCIDR.createAddress(cidr)
        // subnet 미명시 -> /32 추가
        const result = new IPCIDR(address.addressMinusSuffix + address.subnet)
        setCalculatedCidr(result)
    }

    return (
        <div style={{padding: "20px", margin: "0 auto"}}>
            <h1>CIDR Calculator</h1>
            <input
                type="text"
                style={{width: "20%", padding: "10px", marginBottom: "10px"}}
                value={cidr}
                onChange={(e) => setCidr(e.target.value.trim())}
                onKeyDown={(e) => {
                    if (e.key === 'Enter' && !IP.isInvalid(cidr)) {
                        handleCalculateButtonClick()
                    }
                }}
            />
            <button onClick={handleCalculateButtonClick} style={{margin: "10px"}} disabled={IP.isInvalid(cidr)}>Calculate</button>
            {/* result */}
            {R.isNonNullish(calculatedCidr) && (
                <div style={{marginTop: '20px'}}>
                    {/* CIDR Result */}
                    <h2>CIDR Result</h2>
                    <table style={{width: '100%', backgroundColor: '#f0f0f0'}}>
                        <tbody>
                        <tr>
                            <td><strong>Input IP</strong></td>
                            <td>{calculatedCidr.cidr}</td>
                            <td><strong>CIDR Range</strong></td>
                            <td style={{color: 'red'}}>{calculatedCidr.start() + Delimiter.SPACE_HYPHEN_SPACE + calculatedCidr.end()}</td>
                        </tr>
                        <tr>
                            <td><strong>Input IP (Long)</strong></td>
                            <td>{calculatedCidr.address.bigInteger()[0]}</td>
                            <td><strong>CIDR Range (Long)</strong></td>
                            <td>{calculatedCidr.start({type: 'bigInteger'}) + Delimiter.SPACE_HYPHEN_SPACE + calculatedCidr.end({type: 'bigInteger'})}</td>
                        </tr>
                        <tr>
                            <td><strong>Input IP (Hex)</strong></td>
                            <td>{calculatedCidr.address.toHex()}</td>
                            <td><strong>CIDR Range (Hex)</strong></td>
                            <td>{calculatedCidr.addressStart.toHex() + Delimiter.SPACE_HYPHEN_SPACE + calculatedCidr.addressEnd.toHex()}</td>
                        </tr>
                        </tbody>
                    </table>
                    <br/>
                    {/* CIDR Range */}
                    <h2>CIDR Range</h2>
                    <input
                        type="text"
                        placeholder="Enter IP"
                        value={ip}
                        onChange={(e) => setIp(e.target.value)}
                    />
                    <p>
                        Your IP address <span style={{color: 'blue'}}>{ip}</span> is{Delimiter.SPACE}
                        {calculatedCidr!.contains(ip) ?
                            <span style={{color: 'green'}}>inside</span> : <span style={{color: 'red'}}>outside</span>} of the range
                    </p>
                </div>
            )}
        </div>
    )
}